.loader{margin-top:100px;display:flex;align-items:center;flex-direction:column}.loader span{margin-bottom:50px}.loader a{background-color:#1e1e1e;color:#cfcfcf;padding:15px 20px;border-radius:11px;font-size:16px}.loader>*{color:#fff}.lenta{position:relative}.lenta::after{content:"";position:absolute;width:100%;height:100%;background:linear-gradient(45deg, #0c0c0c, rgba(12, 12, 12, 0.3176470588), rgba(12, 12, 12, 0), rgba(12, 12, 12, 0.3176470588), #0c0c0c);z-index:1;pointer-events:none}@keyframes anime{0%{opacity:0}100%{opacity:1}}.lenta span{color:rgba(200,200,200,.2705882353);max-width:10px !important;font-size:30px;font-weight:900;animation:anime 2s ease;user-select:none;transition:.3s}.lenta span:hover{color:#0073ff;cursor:pointer}.lenta .rfm-child{max-width:300px;margin:0 50px}